<template>
  <div>
    <section class="cart_module">
      <section v-if="!foods.specifications.length" class="cart_button">
        <transition name="showReduce">
          <span @click="removeOutCart(foods.category_id,foods.item_id,foods.specfoods[0].food_id,foods.specfoods[0].name,foods.specfoods.price,'',foods.specfoods[0].sku_id,foods.specfoods.stock)" v-if="foodNum">
             <svg>
                 <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#cart-minus"></use>
              </svg>
          </span>
        </transition>
        <transition name="fade">
          <span class="cart_num" v-if="foodNum">{{foodNum}}</span>
        </transition>
        <svg class="add_icon" @touchstart="addToCart(foods.category_id, foods.item_id, foods.specfoods[0].food_id, foods.specfoods[0].name, foods.specfoods[0].price, '', foods.specfoods[0].packing_fee, foods.specfoods[0].sku_id, foods.specfoods[0].stock, $event)">
                <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#cart-add"></use>
        </svg>
      </section>
      <section class="choose_specification" v-else>
        <section class="choose_icon_container">
          <transition name="showReduce">
              <svg class="specs_reduce_icon" v-if="foodNum" @click="showReduceTip">
                        <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#cart-minus"></use>
              </svg>
          </transition>
          <transition name="fade">
            <span class="cart_num" v-if="foodNum">{{foodNum}}</span>
          </transition>
          <span class="show_chooselist" @click="showChooseList(foods)">选规格</span>
        </section>
      </section>
    </section>
  </div>
</template>

<script>
import {mapState,mapMutations} from 'vuex'
export default {
  data(){
    return{
      showMoveDot:[]//控制小圆球下落的隐藏点
    }
  },
  props:['foods','shopId'],
  computed: {
    ...mapState([
      'cartList'
    ]),
    //监听cartList变化更新当前商铺购物车信息shopCart同时返回一个新对象
    shopCart()
    {
      // //方法用于复制可列举的所有的属性的值从一个或多个源对象到目标对象。 它将返回目标对象。
      return Object.assign({},this.cartList[this.shopId]);
    }
    ,
    //变化时重新计算商品数量 当vuex里面购物车数据发生变化立即触发购物车的计算属性然后
    //foodNum引用了shopCart当shopCart计算属性发生变化foodNUm也会执行商品数量就会发生变化
    foodNum(){
      let category_id=this.foods.category_id;
      let item_id=this.foods.item_id;
      if(this.shopCart&&this.shopCart[category_id]&&this.shopCart[category_id][item_id])
      {
        let num=0;
        Object.values(this.shopCart[category_id][item_id]).forEach(item=>{
          num+=item.num;
        })
        return num;
      }else{
        return 0;
      }
    }
  },
  methods: {
    ...mapMutations([
      'ADD_CART','REDUCE_CART'
    ]),
    //移除购物车
    removeOutCart(category_id, item_id, food_id, name, price, specs, packing_fee, sku_id, stock)
    {
      if(this.foodNum>0)
      {
        this.REDUCE_CART({shopid:this.shopId,category_id,item_id,food_id,name,price,specs,packing_fee,sku_id,stock});
      }
    },
    //加入购物车
    addToCart(category_id, item_id, food_id, name, price, specs, packing_fee, sku_id, stock,event)
    {
      this.ADD_CART({shopid:this.shopId,category_id,item_id,food_id,name,price,specs,packing_fee,sku_id,stock});
      //event返回触发事件的DOM节点
      //getBoundingClientRect这个方法返回一个矩形对象，包含四个属性：left、top、right和bottom。分别表示元素各边与页面上边和左边的距离
      let elleft=event.target.getBoundingClientRect().left;
      let elBottom=event.target.getBoundingClientRect().bottom;
      //展示小球下落动画
      this.showMoveDot.push(true);
      this.$emit('showMoveDot',this.showMoveDot,elleft,elBottom);
    },
    //点击多规格商品按钮弹出提示
    showReduceTip()
    {
      this.$emit('showReduceTip');
    },
    //显示规格列表
    showChooseList(foodscroll)
    {
      this.$emit('showChooseList',foodscroll);
    }
  }
}
</script>

<style scopde lang="scss">
@import '../../style/mixin.scss';
.cart_module{
  position: relative;
  z-index: 999;
  .cart_button{
    display: flex;
    align-items: center;//纵轴居中
  }
  svg{
    @include wh(.9rem,.9rem);
    fill: #3190e8;
  }
  .specs_reduce_icon{
    fill:#999;
  }
  .cart_num{
    @include sc(.65rem,#666);
    min-width: 1rem;
    text-align: center;
    font-family: Helvetica Neue,Tahoma;
  }
  .choose_specification{
    .choose_icon_container
    {
      display: flex;
      align-items: center;
      .show_chooselist{
        display: block;
        @include sc(.55rem,#fff);
        padding:.1rem .2rem;
        background: $blue;
        border:0.025rem solid $blue;
        border-radius: 0.2rem;
      }
    }
  }
}
    .showReduce-enter-active, .showReduce-leave-active {
        transition: all .3s ease-out;
    }
    .showReduce-enter, .showReduce-leave-active {
        opacity: 0;
        transform: translateX(1rem);
    }
    .fade-enter-active, .fade-leave-active {
        transition: all .3s;
    }
    .fade-enter, .fade-leave-active {
        opacity: 0;
    }
    .fadeBounce-enter-active, .fadeBounce-leave-active {
        transition: all .3s;
    }
    .fadeBounce-enter, .fadeBounce-leave-active {
        opacity: 0;
        transform: scale(.7);
    }
</style>



